.Loader-overlay {
    position: fixed;
    display: flex;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    pointer-events: none;

    opacity: 0;
    transition: opacity .3s ease-in-out;
}
.Loader-appear-enter-done {
    opacity: 1;
}

.Loader-bar {
    margin: 4px;
    width: 8px;
    height: 30px;

    background: #eee;
    box-shadow: 1px 1px 12px 0px #111;

    animation: load1 1s infinite ease-in-out;
}
.Loader-bar {
    animation-delay: -0.16s;
}
.Loader-bar:first-child {
    animation-delay: -0.32s;
}
.Loader-bar:last-child {
    animation-delay: 0s;
}

@keyframes load1 {
    0%,
    80%,
    100% {
        height: 30px;
    }
    40% {
        height: 55px;
    }
}

.ZoomButton {
    display: flex;
    flex-direction: column;
}
.ZoomButton button {
    margin: 1px;
    padding: 6px 12px;

    cursor: pointer;
    border: none;
    outline: none;

    color: #eee;
    text-shadow: 1px 1px 1px #111;
    background: #0008;
    box-shadow: 1px 1px 4px 0px #000;

    transition: .05s ease-in-out;
}
.ZoomButton button:active {
    transform: translateY(1px);
    box-shadow: none;
}
.ZoomButton button:hover {
    background: #1118;
}
.ZoomButton button:first-child {
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}
.ZoomButton button:last-child {
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}
.MenuButton-hamburger {
    position: relative;
    display: flex;
    justify-content: center;

    height: 40px;
    width: 40px;
    border: 5px solid transparent;

    border-radius: 6px;
    background: #0008;
    box-shadow: 1px 1px 4px 0px #000;

    transition: .3s ease-in-out;
    cursor: pointer;
}
.MenuButton-hamburger:hover {
    background: #1118;
}
.MenuButton-bar {
    position: absolute;

    height: 4px;
    width: 30px;
    display: block;
    background-color: #eee;
    border-radius: 4px;

    transition: .4s ease-in-out;
}
.MenuButton .MenuButton-bar:nth-of-type(1) {
    top: 10px;
    transition: top 0.3s ease 0.3s, transform 0.3s ease-out 0.1s;
}
.MenuButton .MenuButton-bar:nth-of-type(2) {
    top: 18px;
    transition: 0.3s ease 0.3s;
}
.MenuButton .MenuButton-bar:nth-of-type(3) {
    top: 26px;
    transition: top 0.3s ease 0.3s, transform 0.3s ease-out 0.1s;
}
.MenuButton_open .MenuButton-bar:nth-of-type(1) {
    top: 18px;
    transform: rotate(45deg);
    transition: top 0.3s ease 0.1s, transform 0.3s ease-out 0.5s;
}
.MenuButton_open .MenuButton-bar:nth-of-type(2) {
    opacity: 0;
}
.MenuButton_open .MenuButton-bar:nth-of-type(3) {
    top: 18px;
    transform: rotate(-45deg);
    transition: top 0.3s ease 0.1s, transform 0.3s ease-out 0.5s;
}
